import React from 'react';
import makeClass from 'classnames';
import { isAttrTrue } from '@devsnc/sn-controls-common';
import '@servicenow/now-icon';

export default function Label(props) {
	let { htmlFor, children, required, id, reversed } = props;
	let isRequired = isAttrTrue(required);
	return (
		<label
			id={id}
			className={makeClass('sn-control-label', {
				'sn-control-label-reversed': isAttrTrue(reversed)
			})}
			htmlFor={htmlFor}
		>
			<span className="now-line-height-crop">
				<span>{children}</span>
			</span>
			{isRequired && <now-icon size="sm" icon="asterisk-outline" />}
		</label>
	);
}
